import React, { useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'


export const Verify = () => {
  const [val,setVal]=useState(false);
  const [err,setErr]=useState(false);
  let {username}=useParams();

  
  const home=useNavigate()

  const otp_control=()=>{
    
    const otp_value=document.getElementById('otp').value;
    const usernameWithoutDomain = username.split('@')[0];

    if(otp_value==9090){
      
      home(`/map/${username}`)

    }
    else{
          setErr(true);
    }
  }

  const disable=()=>{
    setVal(true);
  }
  const enable=()=>{
    setVal(false);
  }

  return (
    <>
    <div className='ver-container' 
    
    style={{
        backgroundColor: 'rgb(0,21,44)',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        bordeRadius: '5px',
        height: '48vh'
    }}
    >


    <span className='content' style={{display:'flex',
        flexDirection:'column',
        alignItems:'center',
        justifyContent:'center'
    }}>

             <h3 style={{color:'rgb(227,31,251)',fontWeight:'1000'}}>Verify your Email ID</h3> 
             <h4 style={{fontFamily:"sans-serif",color:'white'}}>OTP has been sent to your mail</h4>  
             <p style={{color:'white'}}>{username}</p>                 
                        </span>

<input 
                            type="text" 
                            className='form-control white-placeholder' 
                            placeholder='Your OTP'
                            onFocus={disable}
                            onBlur={enable}
                            id='otp'
                            style={{ 
                                width: '50%', 
                                borderRadius: '25px', 
                                border: 'none', 
                                background:'transparent',
                                color:'white',
                                fontSize:'12px',
                                height: '25px',
                                
                                padding:'10px',
                                outline: 'none', // Remove default outline
                               
                            }}  
                        />
                        <hr style={{ width: '50%',float:'right', height: '3px', 
                         backgroundColor:'white',
                        position: 'relative', margin: '0',display:val?'none':'block' }} />
                        <span style={{color:'red',marginTop:'5px',
                          display:err?'block':'none'
                        }}>Incorrect OTP</span>
                       <button type="button" class="btn btn-primary verbutton"  onClick={otp_control} style={{
                        border:'none',backgroundColor: 'rgb(220,31,251)',width:'51%',
                        marginTop:'10px',borderRadius:'19px',height:'38px'
                       }}
                       
                       > <span style={{
                        fontFamily:'sans-serif',
                        fontWeight:'bold'
                       }}>Submit</span></button>
                     

    </div>

    
    
    </>
  )
}
