import {BrowserRouter,Routes,Route}  from 'react-router-dom';
import { Logintop } from './Components/Logintop';
import { Login } from './Components/Login';
import { Verify } from './Components/Verify';
import GoogleMap from './Components/GoogleMap';
import { Home } from './Components/Home';
import Boxes from './Components/Boxes';
import Attend from './Components/Attend';
import { Settings } from './Components/Settings';


function App() {
  return (
    <>
      <BrowserRouter>
    <Routes>
      <Route>

      <Route path='/' element={<><Logintop/><Login/></>} />
     <Route path='/verify/:username' element={<><Logintop/><Verify/></>} />
     <Route path='/map/:username' element={<GoogleMap/>} />
       
      <Route path='/Home/:username' element={<Home/>}/>
       <Route path='/attendance/:username' element={<><Attend/><Boxes/></>} />
      <Route path='/settings/:username' element={<Settings/>} /> 

       </Route>
    </Routes>
    </BrowserRouter>
    
    </>
  );
}

export default App;
