import React, { useState } from 'react';
import { Bounce, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useNavigate } from 'react-router-dom';

export const Login = () => {
  const [val, setVal] = useState(false);
  const navigate = useNavigate();

 
  const login_control = async () => {
  
    try {
      const username = document.getElementById('username').value;
      if (!username) {
        throw new Error('Username cannot be empty');
      }

      const url = `https://pneoterik.in/trakio/login.php?email=${encodeURIComponent(username)}`;

      // Fetch data from the server
      const response = await fetch(url);

      // Check if the response is ok (status in the range 200-299)
      if (!response.ok) {
        throw new Error('Failed to fetch data from server');
      }

      // Parse the JSON data
      const data = await response.json();
      console.log('Fetched Data:', data);

      if (data.length > 0 && username === data[0].email) {
        console.log(username);
        navigate(`/verify/${username}`);
      } else {
        toast.error('Invalid username');
      }

    } catch (error) {
      // Handle any errors that occurred during the fetch or JSON parsing
      console.error('Error:', error);
      toast.error(error.message);
    }
  };

  const handleInputClick = () => {
    setVal(true);
  };

  const handleInputBlur = () => {
    const username = document.getElementById('username').value;
    setVal(!!username); // Set val to true if input value is not empty
  };

  return (
    <>
      <ToastContainer
        position="top-center"
        autoClose={1000}
        hideProgressBar={true}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
        transition={Bounce}
      />
      <div
        className='login-container'
        style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          borderRadius: '5px',
          position: 'fixed',
          height: '50vh',
          width: '100%',
          backgroundColor: 'rgb(0,21,44)',
          color: 'white'
        }}
      >
      <div style={{ display: 'flex', position: 'relative', width: '60%' }}>
    <input
        type="email"
        className='form-control white-placeholder text-white'
        placeholder='Mail ID'
        onClick={handleInputClick}
        onBlur={handleInputBlur}
        id='username'
        style={{
            width: '100%',
            borderRadius: '25px',
            border: 'none',
            background: 'transparent',
            color: 'white',
            fontSize: '12px',
            height: '25px',
            padding: '10px 30px 10px 10px', // Add padding to the right for the image
            outline: 'none',
        }}
    />
    <img
        src={require('../assets/login-mail.png')}
        width='20px'
        height='20px'
        alt="mail icon"
        style={{
            position: 'absolute',
            right: '10px', // Adjust this value as needed
            top: '50%',
            transform: 'translateY(-50%)', // Center vertically
        }}
    />
</div>

       
        <hr
          style={{
            width: '60%',
            float: 'right',
            height: '3px',
            backgroundColor: 'white',
            position: 'relative',
            margin: '0',
            display: val ? 'none' : 'block'
          }}
        />
        <button
          type="button"
          className="btn btn-primary loginbutton"
          style={{
            backgroundColor: '#45d5ba',
            border: 'none',
            marginTop: '23px',
            width: '60%',
            borderRadius: '20px',
            outline: 'none',
            fontFamily:'monospace',
            fontWeight:'bold',
            fontSize:'20px'
          }}
          onClick={login_control}
        >
          Login
        </button>
        <span style={{ marginTop: '15px', fontSize: '22px' }}>
          Don't have an account? 
          <span style={{ textDecoration: 'underline', color: '#45d5ba', cursor: 'pointer', fontWeight: 'bold' }}>
            Signup
          </span>
        </span>
      </div>
    </>
  );
};
