import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSchool, faHome } from '@fortawesome/free-solid-svg-icons';
import { useParams } from 'react-router-dom';

const FetchAttendance = () => {
    const [attendanceData, setAttendanceData] = useState([]);
    const { username } = useParams();

    useEffect(() => {
        const fetchData = async () => {
            try {
                // Fetch attendance data
                const mob_no_url = `https://pneoterik.in/trakio/fetch_attendance.php?mob_no=${username}`;
                const response = await fetch(mob_no_url);
                if (!response.ok) throw new Error('Network response was not ok');
                const data = await response.json();
                
                // Fetch holidays data
                const scl_id = 1;
                const holidays_url = `https://pneoterik.in/trakio/get_holidays.php?scl_id=${scl_id}`;
                const holidaysResponse = await fetch(holidays_url);
                if (!holidaysResponse.ok) throw new Error('Failed to fetch holidays data');
                const holidaysData = await holidaysResponse.json();
                console.log('grrefrfref',holidaysData);
                

                // Generate last 30 dates
                const last_thirty_Array = [];
                const currentDate = new Date();
                for (let i = 0; i < 30; i++) {
                    const newDate = new Date(currentDate);
                    newDate.setDate(currentDate.getDate() - i);
                    last_thirty_Array.push(newDate.toISOString().slice(0, 10));
                }

                // Initialize attendance data
                const attendanceMap = new Map();
                last_thirty_Array.forEach(date => attendanceMap.set(date, {
                    dates: date,
                    device_id: null, 
                    isLeave: "0",
                    location1: null,
                    location2: null,
                    mob_no: null,
                    name: null,
                    time1: null,
                    time2: null,
                }));

                // Update with fetched attendance data
                data.forEach(item => {
                    if (attendanceMap.has(item.dates)) {
                        attendanceMap.set(item.dates, item);
                    }
                });

                // Handle holidays
                holidaysData.forEach(holiday => {
                    const holidayDate = holiday.hol_days;
                    if (attendanceMap.has(holidayDate)) {
                        const entry = attendanceMap.get(holidayDate);
                        entry.isLeave = "2"; // Assuming "2" indicates a holiday
                        attendanceMap.set(holidayDate, entry);
                    }
                });

                // Convert map to array and sort by date
                const finalData = Array.from(attendanceMap.values()).sort((a, b) => new Date(b.dates) - new Date(a.dates));

                setAttendanceData(finalData);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, [username]);

    return (
        <>
            {attendanceData.map((user, index) => (
                <Attendance
                    key={index}
                    location1={user.location1}
                    location2={user.location2}
                    time1={user.time1}
                    time2={user.time2}
                    stud_id={user.stud_id}
                    isLeave={user.isLeave}
                    dates={user.dates}
                />
            ))}
        </>
    );
};

const Attendance = ({ location1, location2, time1, time2, stud_id, isLeave, dates }) => {
    const formatDate = (dateString) => {
        const date = new Date(dateString);
        const options = { month: 'long', day: 'numeric' };
        return date.toLocaleDateString(undefined, options);
    };

    return (
        <div className="container" style={{
            overflow: 'hidden',
            backgroundColor: 'rgb(224,227,230)',
            display: 'flex',
            flexDirection: 'row',
            width: '90%',
            height: '150px',
            borderRadius: '7px',
            marginTop: '15px',
            margin: '10px 50px 8px 20px',
            paddingTop: '1px',
            boxShadow: '0px 0px 4px rgba(0, 0, 0, 0.4)'
        }}>
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                <div className={isLeave === "1" ? 'pre' : (isLeave === "2" ? 'red' : 'abs')}>
                    <span style={{ fontSize: '20px', fontWeight: '700' }}>{formatDate(dates)}</span>
                </div>
                <span className={isLeave === "2" ? 'leave' : 'notleave'}>Sat/Sun Leave</span>
            </div>
            <div className='icon-container'>
                <div className='icons1'>
                    <span>
                        <FontAwesomeIcon icon={faHome} />
                        <span style={{ marginLeft: '1px' }}>{time1}</span>
                    </span>
                    <span>
                        <FontAwesomeIcon icon={faSchool} />
                        <span style={{ marginLeft: '6px' }}>{time2}</span>
                    </span>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', marginLeft: '3px' }}>
                    <img src={require('../assets/delivery.png')} style={{ width: '30px', height: '30px' }} alt="Delivery Icon" />
                </div>
                <div className='icons1' style={{ marginLeft: '6px' }}>
                    <span>
                        <FontAwesomeIcon icon={faSchool} />
                        <span style={{ marginLeft: '6px' }}>{location1}</span>
                    </span>
                    <span>
                        <FontAwesomeIcon icon={faHome} />
                        <span style={{ marginLeft: '6px' }}>{location2}</span>
                    </span>
                </div>
            </div>
        </div>
    );
};

export const Boxes = () => {
    return (
        <>
            <FetchAttendance />
        </>
    );
};

export default Boxes;
