import React, { useState, useEffect } from 'react';

import { Navigate, Router, Routes,Route, useParams } from 'react-router-dom';


const Attend=()=> {
  const [totalWorkingDays, setTotalWorkingDays] = useState("00");
  const [presentDays, setPresentDays] = useState("00");
  const [absentDays, setAbsentDays] = useState("00");
  const [dates, setDates] = useState([]);
  const [holidays, setHolidays] = useState([]);
  const {username}=useParams();
  let use=username;
  console.log('',use)

  useEffect(() => {
    fetchAndCompareDates();
  }, []);

  const fetchAndCompareDates = async () => {
    try {
      let mob_no=use;
      const get_url=`https://pneoterik.in/trakio/fetch_attendance.php?mob_no=${mob_no}`
      // Fetch attendance data
      const attendanceResponse = await fetch(get_url);
      if (!attendanceResponse.ok) {
        throw new Error('Failed to fetch attendance data');
      }
      const attendanceData = await attendanceResponse.json();
      
      console.log('present',attendanceData);

      // Calculate present days
      let presentCount = 0;
      attendanceData.forEach(item => {
        if (item.isLeave === '1') {
          presentCount++;
        }
      });
      setPresentDays(presentCount);

      let leaveCount = 0;
      attendanceData.forEach(item => {
        if (item.isLeave == '0') {
          leaveCount++;
        }
      });
      
      let scl_id = 1; // Assuming scl_id value

// Construct the URL with the scl_id parameter
const url = `https://pneoterik.in/trakio/get_holidays.php?scl_id=${scl_id}`;
      // Fetch holidays data
      const holidaysResponse = await fetch(url);
      if (!holidaysResponse.ok) {
        throw new Error('Failed to fetch holidays data');
      }
      const holidaysData = await holidaysResponse.json();


      const currentDate = new Date(); // Get the current date
      const pastDate = new Date();
      pastDate.setDate(currentDate.getDate() - 30); // Calculate the date 30 days ago

      // Convert dates to ISO strings for comparison
      const currentDateISO = currentDate.toISOString().split('T')[0];
      const pastDateISO = pastDate.toISOString().split('T')[0];

      const filteredRecords = holidaysData.filter(record => {
          return record.hol_days >= pastDateISO && record.hol_days <= currentDateISO;
      });

      console.log('es5rdyftuygiuoniyfd56',filteredRecords);
      
      const workingDays = attendanceData.length - filteredRecords.length;
      setTotalWorkingDays(workingDays);
      setAbsentDays(leaveCount - filteredRecords.length); // Calculate absent days


    } catch (error) {
      console.error('Error fetching or processing data:', error);
    }
  };

  return (
    <>
      <div className='header'><span className='anal'>Analytics</span></div>
      <div className='labels'>
        <label className='noto-sans-jp- uniquifier'>Attendance</label>
       <a href='#' style={{  color: 'black' }}> <label className='days' style={{ textDecoration: 'underline', cursor: 'pointer',
        fontFamily:'sans-serif'
        }} >
          Last 30 days
        </label></a>
       
      </div>

      <div className='value-container'>
        <div className='boxes'>
          <span style={{ fontSize: '30px', color: 'rgb(96,122,177)', fontWeight: '550' }}>{totalWorkingDays}</span>
          <br />
          <span style={{ marginLeft: '10px', fontWeight: '550' }}>Total Working Days</span>
        </div>
        <div>
          <span style={{ fontSize: '30px', color: 'green', fontWeight: '550' }}>{presentDays}</span>
          <br />
          <span style={{ fontWeight: '550' }}>No.of Days Present</span>
        </div>
        <div>
          <span style={{ fontSize: '30px', color: 'red', fontWeight: '550' }}>{absentDays}</span>
          <br />
          <span style={{ marginRight: '10px', fontWeight: '550' }}>No.of Days Absent</span>
        </div>
      </div>
    </>
  );
}

export default Attend;
