import React, { useState, useEffect } from 'react';
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';
import { BrowserRouter,Link, useNavigate, useParams } from 'react-router-dom';


const GoogleMap = ({ google, deviceId }) => {
    const [lat, setLat] = useState(null);
    const [lng, setLng] = useState(null);
    const[name,setName]= useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const setting=useNavigate();
    const {username}=useParams();
    const user=username.split('@')[0];
    
    
    const fetchLocation2 = async () => {
       let mob_no=user;
                const mob_no_url=`https://pneoterik.in/trakio/fetch_attendance.php?mob_no=${mob_no}`
                const response1 = await fetch(mob_no_url);
                console.log(mob_no);
                
            if (!response1.ok) {
                throw new Error('Failed to fetch location data');       
            }
            const user_data = await response1.json();
            console.log('gghjik', user_data);
            console.log('g', user_data[0].name);
            const device_id=user_data[0].device_id;
            const nam= user_data[0].name;
            let tempname;
         if(true){
          user_data.forEach((item)=>{
              if(item.name!=null){

                   tempname=item.name;
                   console.log('values are the best',item.name);
                   setName(tempname)
              }
            })
         }
        if(tempname==null){
          setName(user);  
        }
         
        }
      
    const fetchLocation = async (device_id) => {
        try {
             
            let mob_no=user;
                const mob_no_url=`https://pneoterik.in/trakio/fetch_attendance.php?mob_no=${mob_no}`
                const response1 = await fetch(mob_no_url);
            if (!response1.ok) {
                throw new Error('Failed to fetch location data');       
            }
            const user_data = await response1.json();
            let device_id;
            user_data.forEach((item)=>{
                if(item.device_id!=null){
                    device_id=item.device_id;
                }
            })
            console.log(device_id);
            
            const url=`https://pneoterik.in/trakio/get_lat_long.php?device_id=${device_id}`
            // const url=`http://thewhizly.co.in/tag/trakio/get_lat_long.php?device_id=1234`
            const response = await fetch(url);

            if (!response.ok) {
                throw new Error('Failed to fetch location data           j');       
            }
            const data = await response.json();
            console.log('Fetched Data:', data);

            if (Array.isArray(data) && data.length > 0) {
                const { latitude, longitude } = data[0];
                setLat(latitude);
                setLng(longitude);
               
                
            } else {
                throw new Error('Invalid data format or empty response');
            }
        } catch (error) {
            console.error('Error fetching data:', error);
            setError('Failed to fetch location data. Please try again later.');
        } finally {
            setLoading(false);
           
        }
    };

    useEffect(() => {
        fetchLocation2();
        fetchLocation();
        
       
    }, [deviceId]);
   

    const onMapClick = (mapProps, map, clickEvent) => {
        // Handle map clicks if needed
    };

    const home=()=>{
        setting(`/home/${user}`)
    }
    const attendance_page=()=>{
        setting(`/attendance/${user}`)
    }
  
    return (
        <div>
            {loading ? (
                <p>Loading...</p>
            ) : error ? (
                <p>Error: {error}</p>
            ) : (
                <div>
                    <div className='top-border'
                    
                    style={{
                        backgroundColor: '#FFC00E',
                        height: '45px'
                    }}
                    >
                        <div style={{ display: 'flex', flexDirection: 'row', float: 'left' }}>
                           
                            <span><img src={require('../assets/bus.png')}width='50px' height='45px' style={{ marginLeft: '5px',
                                padding:'2px'
                             }} /></span><img src={require('../assets/marker.png')} width='10px' height='10px'
                             style={{position:'absolute',marginLeft:'45px',marginTop:'5px'}}/>
                           
                           
                           
                            <span style={{
                                fontSize: '24px',
                                fontWeight: '900',
                                color: 'white',
                                marginLeft: '3px',
                                textShadow: '2px 2px 4px rgba(0, 0, 0, 0.5)'
                            }}>Trak</span>  <span style={{
                                fontSize: '24px',
                                fontWeight: '900',
                                color: ' rgb(185,191,181)',
                                marginLeft: '3px',
                                textShadow: '3px 2px 4px rgba(0, 0, 0, 0.9)'
                            }}>io</span>    
                        </div>
                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', marginRight: '14px',
                            alignItems:'center' }}>
                            <span><img src={require('../assets/contact.png')} width='40px' height='40px' style={{ marginLeft: '5px' }} /></span>
                            <span style={{ fontSize: '20px', fontFamily:'sans-serif', color: 'black' }}> {name}</span>
                        </div>
                    </div>
                    <div style={{ marginTop: '5px' }}>
                        <Map
                            google={google}
                            onClick={onMapClick}
                            style={{
                                
                                justifyContent: 'center',
                                alignItems: 'center',
                                width: '100%',
                                height: '90vh',
                               
                            }}
                            zoom={14}
                            initialCenter={{
                                lat: lat,
                                lng: lng
                            }}
                        >
                            {lat && lng && <Marker position={{ lat, lng }} />}
                        </Map>
                    </div><br></br>
    

<div style={{backgroundColor:'white',height:'10%',width:'100%',bottom:'0',position:'fixed',
  boxShadow:'5px -5px 10px grey',zIndex:'20'
}}>



<button type='btn'  onClick={attendance_page}  className='btn btn-primary' style={{background:'transparent',outline:'none',
          borderColor:'transparent',cursor:'pointer',width:'32%',height:'120%',marginBottom:'10px',margin:'0'
          }}>

       <img src={require('../assets/analytics-lite.png')} width='50px' height='50px'/>

</button>

<button type='btn' className='btn btn-primary' style={{background:'transparent',outline:'none',
          borderColor:'transparent',cursor:'pointer',width:'32%',height:'100%',margin:'0',marginBottom:'40px'
          }}>

                <img src={require('../assets/pin-lite.png')} width='70px' height='70px'/>

</button>

<button type='btn' onClick={home}   className='btn btn-primary' style={{background:'transparent',outline:'none',
          borderColor:'transparent',cursor:'pointer',width:'32%',height:'100%',bottom:'10%',margin:'0',marginBottom:'10px'
          }}>

           <img src={require('../assets/settings-lite.png')} width='50px' height='50px' style={{marginBottom:'10px'}} />

</button>

</div>
</div>
            )}
        </div>
    );
    
};

export default GoogleApiWrapper({
    apiKey: 'AIzaSyAgh_xS1dAhzi12MMB_8m_KrVKGb6ndL0w'
})(GoogleMap);
