import React from 'react';
import Swal from 'sweetalert2';


import { useNavigate, useParams } from 'react-router-dom';

export const Settings = () => {
    const {username}=useParams();
    const user=username;

    const page=useNavigate()
    
   const homepage=()=>{
          page(`/Home/${user}`)
   }


   const update_Data = async () => {
             const update_val=document.getElementById('name').value;
             console.log('updated data'+update_val);
             const mob_no_url=`https://pneoterik.in/trakio/update_stud_details.php?name=${update_val}&mob_no=${user}`
             const response1 = await fetch(mob_no_url);
         if (!response1.ok) {
             throw new Error('Failed to fetch location data');       
         }
       else{
                    Swal.fire({
                        title: "Updated",
                        text: "Successfully!",
                        icon: "success"
                    });

                    page(`/Home/${user}`)
       }
        
    

     }

       

  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      height: '100vh',
      backgroundColor: '#ffffe4'
    }}>  <button onClick={homepage} style={{display:'block',position:'relative',marginBottom:'20px',right:'40%',border:'none',borderRadius:'22px'}}>
    <img src={require('../assets/back-color.png')} width="70px" height="70px" style={{backgroundColor:'white',padding:'15px',
        borderRadius:'40px',boxShadow:'0 0 7px rgba(0, 0, 0, 0.5)'
    }}/>
  </button>
      <div className='set-cont' style={{marginBottom:'55px'}}>
        <h1 style={{
          
          textAlign: 'center',
          color: 'rgb(227, 31, 251)',
          fontWeight: 'bold',
          textShadow: '1px 1px 2px black',
        }}>Update Details</h1>
        
        <div className="form-group">
          <label htmlFor="name" style={{  textAlign: 'left',fontWeight:'bold' }}>Enter the Name:</label>
          <input type='text' id="name" className='form-control ' placeholder='Name' />
        </div>

        <div className="form-group">
          <label htmlFor="schoolId" style={{ textAlign: 'left',fontWeight:'bold' }}>Enter the School ID:<span> (optional)</span></label>
          <input type='text' id="schoolId" className='form-control' placeholder='School ID' />
        </div>

        <button type='btn' onClick={update_Data}  className='btn btn-primary ' style={{ width: '100%',border:'none'
            ,fontWeight:'bold',marginTop:'15px',marginTop:'25px'
         }}>Update</button>
      </div>
    </div>
  );
}; 
